import React, { Component } from 'react';
import {connect} from 'react-redux';
import './App.scss';
import AppRouter from './AppRouter';
import {set as SetBreakpoint} from './reducers/ducks/breakpoint';

class App extends Component {
  constructor(props) {
    super(props);
    this.appRef = React.createRef();
  }

  componentDidMount() {
    this.checkResize()
    window.addEventListener('resize', this.checkResize, true)
  }

  checkResize = () => {
   const root = this.appRef.current
    const newBreakpoint = window.getComputedStyle(root,':after').getPropertyValue('content');
    if(newBreakpoint !== this.breakpoint){
      this.breakpoint = newBreakpoint
      this.props.dispatch(SetBreakpoint(newBreakpoint.replace( /"/g, '')))
    }
  }


  render() {
    return (
      <div className={`App breakpoint-${this.props.breakpoint.value}`} ref={this.appRef}>
        <AppRouter />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    breakpoint: state.rootReducer.breakpoint
  }
}

export default connect(mapStateToProps)(App);



