import React from 'react'
import PropTypes from "prop-types";
require('../style.scss')

export default class Select extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.value
    }
  }

  handleChange = (e) => {
    this.setState({
      value: e.target.value
    })
    this.props.handleChange(this.props.field, e.target.value)
  }

	render() {
    const {label} = this.props
    const {value} = this.state
		return (
      <div className="styled-select">
        <label>{label}</label>
        <select
          value={value}
          onChange={this.handleChange}>
          <option value="week">Questa settimana</option>
          <option value="weekend">Questo weekend</option>
          <option value="monday">Lunedì</option>
          <option value="tuesday">Martedì</option>
          <option value="Wednesday">Mercoleì</option>
          <option value="Thursday">Giovedì</option>
          <option value="Friday">Venerdì</option>
          <option value="Saturday">Sabato</option>
          <option value="Sunday">Domenica</option>
        </select>
      </div>
		)
	}
}

Select.propTypes={
  handleChange: PropTypes.func,
  label: PropTypes.string,
  value: PropTypes.string,
  field: PropTypes.string,
}


