const SET = 'breakpoints/SET'

export function set(value) {
	return {
		type: SET,
		payload: {
			value: value,
		}
	}
}

export default function breakpoints(state = {}, action) {
	switch (action.type) {
		case SET:
			return {
				...state,
				value: action.payload.value,
			}
		default:
			return state;
	}
}


