import React from 'react'

require('./style.scss')

export default class ContentNavBar extends React.Component {
	constructor(props) {
		super(props)
	}

  goBack = () => {
    window.history.back();
  }

	render() {
		return (
			<div className="contentNavBar">
        <div className="container">
          <div className="contentNavBar__container">
            <div className="contentNavBar__left">
              <div onClick={this.goBack} className="button primary">
                <i className="fa fa-chevron-left"/> Torna alla ricerca
              </div>
            </div>
            <div className="contentNavBar__right">
              <a href="#" className="contentNavBar__action">
                Aggiungi ai preferiti <i className="fa fa-star"/>
              </a>
              <a href="#" className="contentNavBar__action">
                Ticket <i className="fa fa-ticket-alt"/>
              </a>
              <a href="#" className="contentNavBar__action">
                Ci vado <i className="fa fa-check-circle"/>
              </a>
            </div>
          </div>
        </div>
      </div>
		)
	}
}

ContentNavBar.propTypes={

}


